body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

input {
  width: 20vw;
  padding: 10px 15px;
  box-sizing: border-box;
}

button {
  background-color: green;
  border-radius: 3px;
  color: white;
  border-style: none;
  font-weight: bold;
  padding: 0.6em;
  font-size: 16px;
}

button:hover {
  background-color: darkgreen;
  cursor: pointer;
}

button:active {
  background-color: darkcyan;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  min-height: 78vh;
}

.App-footer {
  background-color: #282c34;
  min-height: 12vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  color: white;
}

.App-footer > a {
  color: white;
}

.App-link {
  color: #61dafb;
}
